import React, { useState, useEffect, useRef } from 'react';
import { motion } from 'framer-motion';
import { 
  Play, 
  Pause, 
  RotateCcw, 
  Coffee, 
  Target,
  Settings,
  Zap
} from 'lucide-react';
import { toast } from 'react-hot-toast';

const StudyTimer = () => {
  const [mode, setMode] = useState('focus'); // 'focus', 'shortBreak', 'longBreak'
  const [timeLeft, setTimeLeft] = useState(25 * 60); // 25 minutes in seconds
  const [isActive, setIsActive] = useState(false);
  const [isPaused, setIsPaused] = useState(true);
  const [sessions, setSessions] = useState(0);
  const [totalStudyTime, setTotalStudyTime] = useState(0);
  const [showSettings, setShowSettings] = useState(false);
  const [soundEnabled, setSoundEnabled] = useState(true);
  const [autoStartBreaks, setAutoStartBreaks] = useState(false);
  const [currentTask, setCurrentTask] = useState('');
  
  // Timer settings
  const [settings, setSettings] = useState({
    focusTime: 25,
    shortBreakTime: 5,
    longBreakTime: 15,
    sessionsUntilLongBreak: 4
  });

  const intervalRef = useRef(null);
  const audioRef = useRef(null);

  // Initialize audio
  useEffect(() => {
    audioRef.current = new Audio('/notification.mp3'); // You'll need to add this sound file
    audioRef.current.volume = 0.5;
  }, []);

  // Timer logic
  useEffect(() => {
    if (isActive && !isPaused) {
      intervalRef.current = setInterval(() => {
        setTimeLeft(time => {
          if (time <= 1) {
            handleTimerComplete();
            return 0;
          }
          return time - 1;
        });
      }, 1000);
    } else {
      clearInterval(intervalRef.current);
    }

    return () => clearInterval(intervalRef.current);
  }, [isActive, isPaused]);

  // Update document title with timer
  useEffect(() => {
    const minutes = Math.floor(timeLeft / 60);
    const seconds = timeLeft % 60;
    const timeString = `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
    
    if (isActive && !isPaused) {
      document.title = `${timeString} - ${mode === 'focus' ? ' Focus' : ' Break'} - SmartTimetable`;
    } else {
      document.title = 'SmartTimetable';
    }

    return () => {
      document.title = 'SmartTimetable';
    };
  }, [timeLeft, isActive, isPaused, mode]);

  const handleTimerComplete = () => {
    setIsActive(false);
    setIsPaused(true);
    
    // Play notification sound
    if (soundEnabled && audioRef.current) {
      audioRef.current.play().catch(() => {}); // Ignore if sound fails
    }

    if (mode === 'focus') {
      setSessions(prev => prev + 1);
      setTotalStudyTime(prev => prev + settings.focusTime);
      
      // Determine next break type
      const nextSessions = sessions + 1;
      const isLongBreak = nextSessions % settings.sessionsUntilLongBreak === 0;
      const nextMode = isLongBreak ? 'longBreak' : 'shortBreak';
      const nextTime = isLongBreak ? settings.longBreakTime : settings.shortBreakTime;
      
      setMode(nextMode);
      setTimeLeft(nextTime * 60);
      
      toast.success(
        ` Focus session complete! Time for a ${isLongBreak ? 'long' : 'short'} break.`,
        { duration: 5000 }
      );

      if (autoStartBreaks) {
        setTimeout(() => {
          setIsActive(true);
          setIsPaused(false);
        }, 3000);
      }
    } else {
      setMode('focus');
      setTimeLeft(settings.focusTime * 60);
      
      toast.success('Break time over! Ready to focus again?', { duration: 5000 });
    }
  };

  const toggleTimer = () => {
    if (!isActive) {
      setIsActive(true);
      setIsPaused(false);
    } else {
      setIsPaused(!isPaused);
    }
  };

  const resetTimer = () => {
    setIsActive(false);
    setIsPaused(true);
    
    const time = mode === 'focus' ? settings.focusTime : 
                 mode === 'shortBreak' ? settings.shortBreakTime : 
                 settings.longBreakTime;
    setTimeLeft(time * 60);
  };

  const switchMode = (newMode) => {
    setMode(newMode);
    setIsActive(false);
    setIsPaused(true);
    
    const time = newMode === 'focus' ? settings.focusTime : 
                 newMode === 'shortBreak' ? settings.shortBreakTime : 
                 settings.longBreakTime;
    setTimeLeft(time * 60);
  };

  const formatTime = (seconds) => {
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    return `${minutes.toString().padStart(2, '0')}:${remainingSeconds.toString().padStart(2, '0')}`;
  };

  const getProgress = () => {
    const totalTime = mode === 'focus' ? settings.focusTime * 60 : 
                     mode === 'shortBreak' ? settings.shortBreakTime * 60 : 
                     settings.longBreakTime * 60;
    return ((totalTime - timeLeft) / totalTime) * 100;
  };

  const getModeConfig = () => {
    switch (mode) {
      case 'focus':
        return {
          title: 'Focus Time',
          icon: Target,
          color: 'text-red-600',
          bgColor: 'bg-red-50',
          borderColor: 'border-red-200',
          buttonColor: 'bg-red-600 hover:bg-red-700'
        };
      case 'shortBreak':
        return {
          title: 'Short Break',
          icon: Coffee,
          color: 'text-green-600',
          bgColor: 'bg-green-50',
          borderColor: 'border-green-200',
          buttonColor: 'bg-green-600 hover:bg-green-700'
        };
      case 'longBreak':
        return {
          title: 'Long Break',
          icon: Coffee,
          color: 'text-blue-600',
          bgColor: 'bg-blue-50',
          borderColor: 'border-blue-200',
          buttonColor: 'bg-blue-600 hover:bg-blue-700'
        };
      default:
        return {};
    }
  };

  const config = getModeConfig();
  const Icon = config.icon;

  return (
    <div className="max-w-2xl mx-auto p-6 space-y-6">
      {/* Header */}
      <div className="text-center">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">Study Timer</h1>
        <p className="text-gray-600">Stay focused with the Pomodoro Technique</p>
      </div>

      {/* Mode Selector */}
      <div className="flex justify-center space-x-2">
        {[
          { key: 'focus', label: 'Focus', icon: Target },
          { key: 'shortBreak', label: 'Short Break', icon: Coffee },
          { key: 'longBreak', label: 'Long Break', icon: Coffee }
        ].map(({ key, label, icon: ModeIcon }) => (
          <button
            key={key}
            onClick={() => switchMode(key)}
            className={`px-4 py-2 rounded-lg font-medium transition-colors flex items-center gap-2 ${
              mode === key 
                ? 'bg-primary text-white' 
                : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
            }`}
          >
            <ModeIcon className="w-4 h-4" />
            {label}
          </button>
        ))}
      </div>

      {/* Timer Display */}
      <div className={`relative mx-auto w-80 h-80 ${config.bgColor} ${config.borderColor} border-4 rounded-full flex items-center justify-center`}>
        {/* Progress Ring */}
        <svg className="absolute inset-0 w-full h-full transform -rotate-90" viewBox="0 0 100 100">
          <circle
            cx="50"
            cy="50"
            r="45"
            fill="none"
            stroke="currentColor"
            strokeWidth="2"
            className="text-gray-200"
          />
          <circle
            cx="50"
            cy="50"
            r="45"
            fill="none"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            className={config.color}
            strokeDasharray={`${2 * Math.PI * 45}`}
            strokeDashoffset={`${2 * Math.PI * 45 * (1 - getProgress() / 100)}`}
            style={{ transition: 'stroke-dashoffset 1s ease-in-out' }}
          />
        </svg>

        {/* Timer Content */}
        <div className="text-center z-10">
          <Icon className={`w-8 h-8 mx-auto mb-2 ${config.color}`} />
          <div className="text-4xl font-mono font-bold text-gray-900 mb-2">
            {formatTime(timeLeft)}
          </div>
          <div className={`text-sm font-medium ${config.color}`}>
            {config.title}
          </div>
          {currentTask && (
            <div className="text-xs text-gray-600 mt-2 max-w-32 truncate">
              {currentTask}
            </div>
          )}
        </div>
      </div>

      {/* Current Task Input */}
      <div className="text-center">
        <input
          type="text"
          placeholder="What are you working on?"
          value={currentTask}
          onChange={(e) => setCurrentTask(e.target.value)}
          className="input text-center max-w-xs"
        />
      </div>

      {/* Control Buttons */}
      <div className="flex justify-center space-x-4">
        <motion.button
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
          onClick={toggleTimer}
          className={`px-8 py-3 text-white font-medium rounded-lg flex items-center gap-2 ${config.buttonColor}`}
        >
          {isActive && !isPaused ? (
            <>
              <Pause className="w-5 h-5" />
              Pause
            </>
          ) : (
            <>
              <Play className="w-5 h-5" />
              Start
            </>
          )}
        </motion.button>

        <motion.button
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
          onClick={resetTimer}
          className="px-6 py-3 bg-gray-600 hover:bg-gray-700 text-white font-medium rounded-lg flex items-center gap-2"
        >
          <RotateCcw className="w-5 h-5" />
          Reset
        </motion.button>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-3 gap-4 text-center">
        <div className="bg-white rounded-lg p-4 shadow-sm border">
          <div className="text-2xl font-bold text-gray-900">{sessions}</div>
          <div className="text-sm text-gray-600">Sessions</div>
        </div>
        <div className="bg-white rounded-lg p-4 shadow-sm border">
          <div className="text-2xl font-bold text-gray-900">{Math.floor(totalStudyTime / 60)}h {totalStudyTime % 60}m</div>
          <div className="text-sm text-gray-600">Study Time</div>
        </div>
        <div className="bg-white rounded-lg p-4 shadow-sm border">
          <div className="text-2xl font-bold text-gray-900">{sessions % settings.sessionsUntilLongBreak}/{settings.sessionsUntilLongBreak}</div>
          <div className="text-sm text-gray-600">Until Long Break</div>
        </div>
      </div>

      {/* Settings */}
      <div className="text-center">
        <button
          onClick={() => setShowSettings(!showSettings)}
          className="btn btn-outline flex items-center gap-2 mx-auto"
        >
          <Settings className="w-4 h-4" />
          Settings
        </button>
      </div>

      {showSettings && (
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="bg-white rounded-lg p-6 shadow-lg border space-y-4"
        >
          <h3 className="text-lg font-semibold text-gray-900">Timer Settings</h3>
          
          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Focus Time (minutes)
              </label>
              <input
                type="number"
                min="1"
                max="60"
                value={settings.focusTime}
                onChange={(e) => setSettings(prev => ({ ...prev, focusTime: parseInt(e.target.value) || 25 }))}
                className="input"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Short Break (minutes)
              </label>
              <input
                type="number"
                min="1"
                max="30"
                value={settings.shortBreakTime}
                onChange={(e) => setSettings(prev => ({ ...prev, shortBreakTime: parseInt(e.target.value) || 5 }))}
                className="input"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Long Break (minutes)
              </label>
              <input
                type="number"
                min="1"
                max="60"
                value={settings.longBreakTime}
                onChange={(e) => setSettings(prev => ({ ...prev, longBreakTime: parseInt(e.target.value) || 15 }))}
                className="input"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Sessions Until Long Break
              </label>
              <input
                type="number"
                min="2"
                max="10"
                value={settings.sessionsUntilLongBreak}
                onChange={(e) => setSettings(prev => ({ ...prev, sessionsUntilLongBreak: parseInt(e.target.value) || 4 }))}
                className="input"
              />
            </div>
          </div>

          <div className="space-y-3">
            <label className="flex items-center">
              <input
                type="checkbox"
                checked={soundEnabled}
                onChange={(e) => setSoundEnabled(e.target.checked)}
                className="mr-2"
              />
              <span className="text-sm text-gray-700">Enable notification sounds</span>
            </label>
            
            <label className="flex items-center">
              <input
                type="checkbox"
                checked={autoStartBreaks}
                onChange={(e) => setAutoStartBreaks(e.target.checked)}
                className="mr-2"
              />
              <span className="text-sm text-gray-700">Auto-start breaks</span>
            </label>
          </div>
        </motion.div>
      )}

      {/* Tips */}
      <div className="bg-blue-50 rounded-lg p-4 border border-blue-200">
        <h4 className="font-medium text-blue-900 mb-2 flex items-center gap-2">
          <Zap className="w-4 h-4" />
          Pomodoro Tips
        </h4>
        <ul className="text-sm text-blue-800 space-y-1">
          <li> Focus completely during work sessions</li>
          <li> Take breaks seriously - step away from your desk</li>
          <li> Use breaks for light activities like stretching</li>
          <li> Track what you accomplish in each session</li>
        </ul>
      </div>
    </div>
  );
};

export default StudyTimer;
